@import 'src/common/styles/sass/variables';

.loader {
  display: block;
  text-align: center;
  text-transform: uppercase;
  font-size: 28px;
  color: transparent;
  letter-spacing: 0.01em;
  margin: calc(1rem * calc(1)) auto auto;

  span {
  text-shadow: 0 0 2px rgba(114, 226, 174, 1),
  0 15px 25px rgba(114, 226, 174, 0.3),
  0 -2px 3px rgba(114, 226, 174, 0.1),
  0 -5px 10px rgba(114, 226, 174, 0.5),
  0 5px 10px rgba(114, 226, 174, 0.3),
  0 3px 4px rgba(114, 226, 174, 0.2),
  0 0 20px rgba(114, 226, 174, 0.45);

  animation: letters 0.85s, 0, ease-in-out, infinite, alternate;

  &:nth-child(2) {
    animation-delay: 0.15s;
  }

  &:nth-child(3) {
    animation-delay: 0.30s;
  }

  &:nth-child(4) {
    animation-delay: 0.45s;
  }

  &:nth-child(5) {
    animation-delay: 0.60s;
  }

  &:nth-child(6) {
    animation-delay: 0.75s;
  }

  &:nth-child(7) {
    animation-delay: 0.90s;
  }
}
}

@keyframes letters {
  to {
    text-shadow: 0 0 2px rgba(114, 226, 174, 0.2),
    0 0 3px rgba(114, 226, 174, 0.02),
    0 0 0 rgba(114, 226, 174, 0),
    0 0 0 rgba(114, 226, 174, 0),
    0 0 0 rgba(114, 226, 174, 0),
    0 0 0 rgba(114, 226, 174, 0),
    0 0 0 rgba(114, 226, 174, 0);
  }
}
@-moz-keyframes letters {
  to {
    text-shadow: 0 0 2px rgba(114, 226, 174, 0.2),
    0 0 3px rgba(114, 226, 174, 0.02),
    0 0 0 rgba(114, 226, 174, 0),
    0 0 0 rgba(114, 226, 174, 0),
    0 0 0 rgba(114, 226, 174, 0),
    0 0 0 rgba(114, 226, 174, 0),
    0 0 0 rgba(114, 226, 174, 0);
  }
}
@-webkit-keyframes letters {
  to {
    text-shadow: 0 0 2px rgba(114, 226, 174, 0.2),
    0 0 3px rgba(114, 226, 174, 0.02),
    0 0 0 rgba(114, 226, 174, 0),
    0 0 0 rgba(114, 226, 174, 0),
    0 0 0 rgba(114, 226, 174, 0),
    0 0 0 rgba(114, 226, 174, 0),
    0 0 0 rgba(114, 226, 174, 0);
  }
}